.how-it-works {
  padding-top: 29px;
  padding-bottom: 20px;

  @media (min-width: $tablet-width) {
    padding-top: 45px;
    padding-bottom: 0;
    border-bottom: 1px solid $gray-2;
  }

  @media (min-width: $desktop-width) {
    padding-top: 72px;
    border: none;
  }
}

.how-it-works__title {
  margin-bottom: 42px;
}

.how-it-works__list {
  @include mp-zero;
  @include font(14px, 18px);

  font-family: $ff-arial;
  list-style: none;
  color: $dark-gray-2;
  counter-reset: section;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-right: 63px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 0;
  }
}

.how-it-works__item {
  position: relative;
  padding-left: 80px;
  margin-bottom: 26px;

  @media (min-width: 620px) {
    margin-bottom: 45px;
  }

  @media (min-width: $tablet-width) {
    @include font(16px, 24px);

    width: 260px;
    padding-top: 143px;
    padding-left: 0;
    margin-bottom: 102px;
  }

  &::before {
    content: "";
    position: absolute;
    top: -2px;
    left: 0;
    width: 60px;
    height: 60px;

    @media (min-width: 620px) {
      top: -14px;
    }

    @media (min-width: $tablet-width) {
      top: 40px;
      left: 10px;
      transform: scale(1.35);
    }
  }

  &--healthy::before {
    background-image: url("../img/icons/icon-healthy.svg");
  }

  &--powder::before {
    background-image: url("../img/icons/icon-powder.svg");
  }

  &--one-meal::before {
    background-image: url("../img/icons/icon-one-meal.svg");
  }

  &--changes::before {
    background-image: url("../img/icons/icon-changes.svg");
  }

  &::after {
    @media (min-width: $tablet-width) {
      position: absolute;
      z-index: -10;
      left: 112px;
      top: 120px;
      font-family: $ff-oswald;
      font-size: 280px;
      color: $gray-1;
      counter-increment: section;
      content: counter(section);
    }
  }
}

.how-it-works__item:last-child {
  @media (min-width: $desktop-width) {
    margin-right: -15px;
  }
}
