.social {
  @include mp-zero;

  display: flex;
  justify-content: center;
  width: 100%;
  padding: 16px 0;
  margin: 11px 0 4px;
  border-top: 1px solid $dark-gray-3;
  border-bottom: 1px solid $dark-gray-3;
  list-style: none;

  @media (min-width: $tablet-width) {
    @include mp-zero;

    border: none;
    margin-left: 17px;
  }

  @media (min-width: $desktop-width) {
    @include mp-zero;

    border: none;
    margin-left: 0;
  }
}

.social__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 28px;

  @media (min-width: $tablet-width) {
    width: 35px;
  }
}

.social__item:not(:last-child) {
  margin-right: 34px;

  @media (min-width: $tablet-width) {
    margin-right: 23px;
  }
}


.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 2px;
  left: 9px;


  &:hover .social__img {
    fill: $green;
  }

  &:active path {
    opacity: 0.3;
  }
}

.social__img {
  fill: $dark-gray-1;
}

.social__link--inst {
  top: 3px;
}

.social__link--fb {
  top: -3px;
  left: -4px;
}
