.map {
  @media (min-width: $desktop-width) {
    position: relative;
  }
}

.map__text {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 28px;
  padding-bottom: 26px;
  z-index: 20;

  @media (min-width: $tablet-width) {
    padding: 65px 70px 58px 70px;
  }

  @media (min-width: 1360px) {
    max-width: 100%;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 96px;
    left: 110px;
    width: 428px;
    padding-left: 80px;
  }

  &--bg-white {
    @include bg-color($white);
  }

  &--bg-gray {
    @media (max-width: $desktop-width) {
      @include bg-color($gray-1);
    }

    @media (min-width: $desktop-width) {
      @include bg-color($white);
    }
  }
}

.map__text-container {
  @media (min-width: $desktop-width) {
    position: relative;
    max-width: 1220px;
    padding-left: 110px;
    padding-right: 110px;
    margin: 0 auto;
  }
}

.map__title {
  @include mp-zero;
  @include font(16px, 20px);

  width: 180px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    @include font(20px, 26px);
  }
}

.map__desc {
  @include mp-zero;
  @include font(14px, 20px);

  display: flex;
  flex-direction: column;
  width: 180px;
  color: $dark-gray-2;
  font-family: $ff-arial;

  @media (min-width: $tablet-width) {
    @include font(16px, 20px);

    text-align: right;
  }

  @media (min-width: $desktop-width) {
    @include font(16px, 20px);

    text-align: left;
  }
}

.map__address-city {
  @media (min-width: $tablet-width) {
    padding-top: 18px;
  }
}

.map__map {
  width: 100%;
  height: 362px;
  background-image: url("../img/bg/map-bg-mobile.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/bg/map-bg-mobile@2x.png");
  }

  @media (min-width: $tablet-width) {
    height: 400px;
    background-image: url("../img/bg/map-bg-tablet.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/bg/map-bg-tablet@2x.png");
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/bg/map-bg-desktop.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/bg/map-bg-desktop@2x.png");
    }
  }
}

.map__img {
  width: 100%;
  height: 362px;
  object-fit: cover;

  @media (min-width: $tablet-width) {
    height: 400px;
  }
}
