.navigation {
  position: relative;
}
@media (min-width: 768px) {
  .navigation {
    display: block;
    flex-direction: row;
    justify-content: space-between;
  }
}

.navigation__burger {
  background-color: transparent;
  display: none;
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 2;
  width: 70px;
  height: 50px;
  cursor: pointer;
  border: none;
}
.navigation__burger--visible {
  display: block;
}
@media (min-width: 768px) {
  .navigation__burger {
    display: none;
  }
}

.navigation__burger-line {
  background-color: black;
  position: absolute;
  top: 18px;
  left: 26px;
  width: 24px;
  height: 2px;
  transition: all 0.3s ease-out;
}
.navigation__burger-line--closed::before, .navigation__burger-line--closed::after {
  background-color: black;
  content: "";
  position: absolute;
  bottom: 8px;
  left: 0;
  width: 24px;
  height: 2px;
  transition: all 0.3s ease-out;
}
.navigation__burger-line--closed::after {
  top: 8px;
}
.navigation__burger-line--opened {
  background-color: transparent;
}
.navigation__burger-line--opened::before, .navigation__burger-line--opened::after {
  background-color: black;
  content: "";
  position: absolute;
  top: -1px;
  left: 0;
  width: 25px;
  height: 2px;
  transform: rotate(45deg);
  transition: all 0.3s ease-out;
}
.navigation__burger-line--opened::after {
  transform: rotate(-45deg);
}

.navigation__list {
  margin: 0;
  padding: 0;
  background-color: white;
  list-style: none;
}
.navigation__list--closed {
  display: none;
}
.navigation__list--opened {
  display: block;
  position: absolute;
  z-index: 10;
  width: 100%;
  box-shadow: 0 8px 10px #666666;
}
@media (min-width: 768px) {
  .navigation__list {
    background-color: transparent;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-wrap: wrap;
    align-items: center;
    min-width: 460px;
    max-width: 500px;
    height: 100%;
  }
}
@media (min-width: 1440px) {
  .navigation__list {
    max-width: 600px;
  }
}

.navigation__item {
  font-size: 20px;
  line-height: 20px;
  font-weight: 400;
  position: relative;
  padding-top: 23px;
  padding-bottom: 23px;
  text-transform: uppercase;
  text-align: center;
  border-top: 1px solid #ebebeb;
}
@media (min-width: 768px) {
  .navigation__item {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    margin-left: 35px;
    border-top: none;
  }
}
@media (min-width: 1440px) {
  .navigation__item {
    margin-left: 54px;
  }
}
@media (min-width: 768px) {
  .navigation__item:hover .navigation__link {
    color: rgba(0, 0, 0, 0.6);
  }
}
@media (min-width: 768px) {
  .navigation__item:active .navigation__link {
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 768px) {
  .navigation__item:hover::after {
    background-color: #68b738;
    content: "";
    position: absolute;
    left: 0;
    bottom: 14px;
    width: 100%;
    height: 2px;
    animation: underline 0.4s ease-out;
  }
}
@media (min-width: 1440px) {
  .navigation__item--white:hover .navigation__link {
    color: rgba(255, 255, 255, 0.6);
  }
}
@media (min-width: 1440px) {
  .navigation__item--white:active .navigation__link {
    color: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 1440px) {
  .navigation__item--white:hover::after {
    background-color: white;
    content: "";
    position: absolute;
    left: 0;
    bottom: 14px;
    width: 100%;
    height: 2px;
    animation: underline 0.4s ease-out;
  }
}
@media (min-width: 1440px) {
  .navigation__item--white .navigation__link {
    color: white;
  }
}
.navigation__item--current {
  position: relative;
}
@media (min-width: 768px) {
  .navigation__item--current::after {
    background-color: #68b738;
    content: "";
    position: absolute;
    left: 0;
    bottom: 14px;
    width: 100%;
    height: 2px;
    animation: underline 0.4s;
  }
}
@media (min-width: 768px) {
  .navigation__item--white-current::after {
    background-color: #68b738;
    content: "";
    position: absolute;
    left: 0;
    bottom: 14px;
    width: 100%;
    height: 2px;
    animation: underline 0.4s;
  }
}
@media (min-width: 1440px) {
  .navigation__item--white-current::after {
    background-color: white;
  }
}

