.extra-products__cost {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  font-family: "Arial", "Helvetica", sans-serif;
  color: #222222;
}
@media (min-width: 768px) {
  .extra-products__weight,
.extra-products__cost {
    font-size: 16px;
    line-height: 16px;
    font-weight: 400;
  }
}

@media (min-width: 768px) {
  .extra-products__cost {
    margin-left: 53px;
  }
}

.gift {
  padding-bottom: 36px;
}
@media (min-width: 768px) {
  .gift {
    padding-bottom: 76px;
  }
}
@media (min-width: 1440px) {
  .gift {
    padding-bottom: 0;
    padding-top: 172px;
  }
}

@media (min-width: 1440px) {
  .gift__container {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
  }
}

.gift__wrapper {
  background-color: #68b738;
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  padding-top: 42px;
  height: 289px;
  width: 100%;
  background: url("../img/bg/cat-gift-mobile.jpg") center no-repeat;
  background-size: cover;
  text-align: center;
}
@media (min-resolution: 2dppx) {
  .gift__wrapper {
    background: url("../img/bg/cat-gift-mobile@2x.jpg") center no-repeat;
    background-size: cover;
  }
}
@media (min-width: 768px) {
  .gift__wrapper {
    padding-top: 0;
    padding-left: 80px;
    padding-right: 140px;
    height: 200px;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 768px) and (max-width: 1440px) {
  .gift__wrapper {
    background: url("../img/bg/cat-gift-tablet.jpg") center no-repeat;
    background-size: cover;
  }
}
@media (min-width: 768px) and (max-width: 1440px) and (min-resolution: 2dppx) {
  .gift__wrapper {
    background: url("../img/bg/cat-gift-tablet@2x.jpg") center no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1440px) {
  .gift__wrapper {
    padding: 45px 0 10px 0;
    width: 245px;
    height: 290px;
    flex-direction: column;
    justify-content: space-around;
  }
}

.gift__title {
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  width: 60%;
  font-family: "Arial", "Helvetica", sans-serif;
  color: white;
}
@media (min-width: 768px) {
  .gift__title {
    width: 30%;
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .gift__title {
    width: 60%;
    text-align: center;
  }
}

.main-catalog {
  overflow: hidden;
}

@media (min-width: 1440px) {
  .main-catalog__grid-wrapper {
    margin-bottom: 85px;
  }
}

.main-catalog__grid-wrapper-container {
  padding-left: 0;
  padding-right: 0;
}
@media (min-width: 1440px) {
  .main-catalog__grid-wrapper-container {
    display: grid;
    grid-template-columns: 1fr 245px;
    column-gap: 80px;
  }
}