@mixin bg-color ($color) {
  background-color: $color;
}

@mixin size ($width, $height) {
  width: $width;
  height: $height;
}

@mixin mp-zero {
  margin: 0;
  padding: 0;
}

@mixin font($font-size, $line-height: $font-size, $font-weight: 400) {
  font-size: $font-size;
  line-height: $line-height;
  font-weight: $font-weight;
}

@mixin animation ($animation-name, $animation-time, $animation-func: ease-out) {
  animation: $animation-name $animation-time $animation-func;
}
